
import {  formatDate } from '@angular/common';
export class ThietBiModel {
    Ma: string
    MaThietBi: string
    Ten: string
    IDThietBiChiTiet: string
    MaThietBiChiTiet: string
    TenTinhTrang: string
    TenNoiQuanLy: string
    SuDungToiDa: number
    SoLuongDangSuDung: number
    
    TenNuocSanXuat: string
    
    constructor() {
        this.Ma = ""
        this.Ten = ""
        
        this.TenNoiQuanLy = ""
    }
    copy(input: ThietBiReqModel){
        this.Ma = input.Ma
        this.Ten = input.Ten
        this.TenTinhTrang = input.TenTinhTrang
        this.SuDungToiDa = input.SuDungToiDa
        this.SoLuongDangSuDung = input.SoLuongDangSuDung
        this.TenNuocSanXuat = input.TenNuocSanXuat
    }
}
export class ThietBiReqModel extends ThietBiModel{
    SoLuong: number
    NamSuDung: number
    MaNoiQuanLy: string
    MaNuocSanXuat: string
    MaTinhTrang: string
    HienThi: Boolean
    constructor(ma: string, ten: string, soLuong: number) {
        super()
        this.Ma = ma
        this.Ten = ten
        this.SoLuong = soLuong
        this.NamSuDung = Number(formatDate(new Date(), 'yyyy', 'en'))
        this.MaNuocSanXuat = ''
        this.MaNoiQuanLy = ""
        this.HienThi = true
    }
}